
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
    scroll-behavior: smooth;
}

html, body, #root {
    height: 100%;
}

@keyframes fadeInLong {
    0% {
        opacity: 0;
    }
    7% {
        opacity: 0;
    }
    15% {
        opacity: 0.3;
    }
    30% {
        opacity: 0.4;
    }
    45% {
        opacity: 0.5;
    }
    60% {
        opacity: 0.6;
    }
    75% {
        opacity: 0.7;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes bounceArrow
{
    0%, 20%, 50%, 80%, 100% {
        transform:translateY(0) rotate(45deg)
    }
    40% {
        transform:translateY(-3px) rotate(45deg)
    }
    60% { 
        transform:translateY(-5px) rotate(45deg)
    }
}


@keyframes increase
{
    0% {
        transform: translateY(1000px);
    }
    100% { 
        transform: none;
    }
}

@keyframes blink
{
    0% {
        transform: translateY(1000px);
    }
    50% { 
        transform: none;
    }
    51% {
        opacity: 1;
    }
    55% {
        opacity: 0;
    }
    90% {
        opacity: 1;
    }
    100% { 
        opacity: 0.1;
    }
}

@keyframes blink2
{
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    40% {
        opacity: 0;
    }
    60% { 
        opacity: 1;
    }
    100% { 
        opacity: 0;
    }
}

.container {
    background: url(./../img/stars_00000.png) black;
    background-size: 100%;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    .noise {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: url(./../img/glitch_inc.gif);
        background-size: cover;
        opacity: 0.1;
    }
    .header {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        .circle {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 50%;
            transform: translate(-50%, -50%);
            // max-width: 480px;
            // width: 40%;
            opacity: 1;
            animation: fadeInLong 3s;
            &.hide {
                animation: fadeOut 5s;
                opacity: 0;
            }
        }
        .name {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 28%;
            transform: translate(-90%, -90%);
            opacity: 1;
            animation: fadeIn 5s;
            // min-width: 260px;
            &.hide {
                animation: fadeOut 3s;
                opacity: 0;
            }
        }
        .letters {
            position: absolute;
            top: 57%;
            left: 53%;
            width: 40%;
            transform: translate(-50%, -30%);
            opacity: 1;
            animation: fadeIn 8s;
            // min-width: 370px;
            &.hide {
                animation: fadeOut 4s;
                opacity: 0;
            }
        }
        .btn-scroll {
            position: absolute;
            display: flex;
            align-items: center;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            color: white;
            background: none;
            border: none;
            font-size: 2em;
            padding: 30px;
            z-index: 5;
            cursor: pointer;
            animation: fadeIn 10s;
            opacity: 1;
            transition: opacity 1s;
            &.hide {
                opacity: 0;
                transition: opacity 0.5s;
            }
            span:not(.arrow) {
                margin: 0 10px;
            }
            .arrow {
                display: block;
                width: 18px;
                min-width: 18px;
                min-height: 18px;
                height: 18px;
                border-right: 3px solid;
                border-bottom: 3px solid;
                border-color: #d1d1d1;
                transform: rotate(45deg);
                margin: 0 10px;
                animation: bounceArrow 3s infinite;
            }
        }
    }
    .header {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        .description-container {
            position: absolute;
            z-index: 100;
            top: 0;
            left: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            flex-direction: column;
            &.show {
                animation: fadeIn 2s;
                .logo {
                    // transition: opacity 2s 3s;
                    // opacity: 1;
                    opacity: 1;
                    animation: fadeIn 2s;
                }
                .text {
                    // transition: opacity 2.5s 10s;
                    opacity: 1;
                    animation: fadeIn 5s;
                }
                .sponsors {
                    opacity: 1;
                    animation: fadeIn 8s;
                }
            }
            .logo {
                height: 15%;
                opacity: 1;
                // background-image: url(./../img/logo.png);
                img {
                    height: 100%;
                }
            }
            .sponsors {
                height: 13%;
                margin-top: 50px;
                opacity: 0;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: center;
                padding: 0 20px;
                img {
                    height: 100%;
                    margin-bottom: 25px;
                }
            }
            .text {
                color: white;
                display: flex;
                flex-direction: column;
                align-items: center;
                font-size: 2.7em;
                padding-top: 40px;
                opacity: 0;
                span {
                    font-family: 'Roboto', sans-serif;
                    display: inline-flex;
                    white-space: nowrap;
                    padding: 5px;
                    p {
                        white-space: nowrap;
                        color: #00cb09;
                    }
                }

            }
        }
        .symb-area {
            position: fixed;
            width: 50%;
            height: 100%;
            img {
                position: absolute;
                width: 20%;
            }
            &.show {
                opacity: 1;
                transition: opacity 1s;
                animation: fadeIn 2s;
                img {
                    animation: fadeIn 4s;
                }
                img:nth-child(even) {
                    animation: blink2 8s infinite;
                    opacity: 0.5;
                }
            }
            &.show-2 {
                opacity: 1;
                transition: opacity 1s;
                animation: fadeIn 2s;
                img {
                    animation: fadeIn 4s;
                }
                img:nth-child(even) {
                    animation: blink2 10s infinite;
                    opacity: 0.5;
                }
            }
            &.show-3 {
                img {
                    width: 13%;
                    animation: fadeIn 10s;
                    opacity: 0.4;
                }
                img:nth-child(even) {
                    // animation: blink 16s 2s;
                    opacity: 0.3;
                }
            }
            &.show-4 {
                img {
                    width: 14%;
                    animation: fadeIn 12s;
                    opacity: 0.4;
                    // animation: increase 8s 2s;
                    
                }
                img:nth-child(even) {
                    // animation: blink2 7s infinite;
                    opacity: 0.4;
                }
            }
        }
    }
}




