    }
    .container .header .letters {
        width: 74%;
        top: 62%;
    }

    .container .header .description-container .sponsors {
        height: 10%;
    }
}


@media (max-width: 700px) { 

    .container .header .circle {
        width: 100%;
    }
    .container .header .name {
        width: 50%;
        top: 43%;
        left: 59%;
    }
    .container .header .letters {

        width: 94%;
        top: 58%;
    
    }

    .container .header .description-container .text {
        font-size: 2em;
    }

    .container .header .description-container .sponsors {
        height: 8%;
    }

    .container .header .symb-area.show-3 img, .container .header .symb-area.show-4 img {
        width: 35%;
    }

}

@media (max-width: 500px) { 
    .container .header .description-container.show .sponsors {
        height: 8%;
    }
    .container .header .description-container .text {
        font-size: 1.5em;
    }
}



